.employeeWrapper {
  padding: 0 30px;
  width: 100%; }
  @media only screen and (min-width: 680px) {
    .employeeWrapper {
      width: 50%; } }
  @media only screen and (min-width: 1024px) {
    .employeeWrapper {
      width: 33.33%; } }

.employee {
  display: block;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  margin-bottom: 40px; }

.employee a:hover .imageWrapper:after {
  height: 100%; }

.employee a:hover .content {
  bottom: 100px; }

.employee a:hover .position {
  opacity: 1; }

.imageWrapper {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 100%; }
  .imageWrapper:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 100%;
    background-color: transparent;
    border-radius: 50%;
    border: 15px solid rgba(32, 39, 68, 0.7); }
  .imageWrapper:after {
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 33%;
    background-color: rgba(32, 39, 68, 0.7); }
  .imageWrapper img {
    position: absolute;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    z-index: 1;
    border-radius: 50%; }

.content {
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  position: absolute;
  width: 100%;
  z-index: 2;
  bottom: 10px;
  text-align: center; }

.title {
  margin: 0 0 10px 0;
  font-size: 26px;
  color: #fff; }

.position {
  opacity: 0;
  font-size: 16px;
  color: #fff; }
  .position:after {
    content: ", "; }
  .position:last-child:after {
    display: none; }

.bio {
  font-size: 16px;
  color: #595f6b; }

.socialMedia {
  transition: all 0.3s ease-out;
  margin: 0;
  padding: 0;
  position: absolute;
  transform: translateY(60px);
  height: 60px;
  width: 100%;
  background-color: #e5e5e5; }



